import * as React from 'react';
import PropTypes from 'prop-types';
import AppBar from '@mui/material/AppBar';
import Box from '@mui/material/Box';
import CssBaseline from '@mui/material/CssBaseline';
import Divider from '@mui/material/Divider';
import Drawer from '@mui/material/Drawer';
import IconButton from '@mui/material/IconButton';
import { Link } from 'react-router-dom';
import MenuIcon from '@mui/icons-material/Menu';
import Toolbar from '@mui/material/Toolbar';
import Typography from '@mui/material/Typography';
import Button from '@mui/material/Button';
import '../Styles/Header1.css'; // import CSS file

const drawerWidth = 240;
// const navItems = ['Introduction','Research','Broader Impacts', 'Publications' ,'Group' ,'Facilities' ,'Teaching'];

function DrawerAppBar(props) {
  const { window } = props;
  const [mobileOpen, setMobileOpen] = React.useState(false);

  const handleDrawerToggle = () => {
    setMobileOpen((prevState) => !prevState);
  };

  const drawer = (
    <Box onClick={handleDrawerToggle} sx={{ textAlign: 'center' }}>
      <Typography variant="h6" sx={{ my: 2 }}>
        MUI
      </Typography>
      <Divider />
      <ul>
          <li>
            <Link to="/">
              <Typography variant="h6" sx={{ my: 2 }}>
                  Introduction
              </Typography>
            </Link>
          </li>
          {/* <li>
            <Link to="/Research">
              <Typography variant="h6" sx={{ my: 2 }}>
                Research
              </Typography>
            </Link>
          </li> */}
          <li>
            <Link to="/BorderImpacts">
              <Typography variant="h6" sx={{ my: 2 }}>
                Border Impacts
              </Typography>
            </Link>
          </li>
          {/* <li>
            <Link to="/Publications">
              <Typography variant="h6" sx={{ my: 2 }}>
                Publications
              </Typography>
            </Link>
          </li>
          <li>
            <Link to="/Group">
              <Typography variant="h6" sx={{ my: 2 }}>
                Group
              </Typography>
            </Link>
          </li>
          <li>
            <Link to="/Facilities">
              <Typography variant="h6" sx={{ my: 2 }}>
                Facilities
              </Typography>
            </Link>
          </li>
          <li>
            <Link to="/Teaching">
              <Typography variant="h6" sx={{ my: 2 }}>
                Teaching
              </Typography>
            </Link>
          </li> */}
      </ul>
    </Box>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <Box sx={{ display: 'flex' }}>
      <CssBaseline />
      <AppBar component="nav">
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          <Box sx={{ display: { xs: 'none', sm: 'block' } ,marginInlineStart: 'auto'}}>
            <Link to="Introduction">
              <Button key="Introduction" sx={{ color: '#fff' }}>
                Introduction
              </Button>
            </Link>
            <Link to="BroaderImpacts">
              <Button key="BroaderImpacts" sx={{ color: '#fff' }}>
                Broader Impacts
              </Button>
            </Link>
          </Box>
        </Toolbar>
      </AppBar>
      <Box component="nav">
        <Drawer
          container={container}
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': { boxSizing: 'border-box', width: drawerWidth },
          }}
        >
          {drawer}
        </Drawer>
      </Box>
    </Box>
  );
}

DrawerAppBar.propTypes = {
  window: PropTypes.func,
};

export default DrawerAppBar;